#App {
  text-align: center;
  color: white;
}

.florange-logo {
  height: 400px;
  pointer-events: none;
  user-select: none;
}

.App-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  font-size: calc(10px + 2vmin);
  color: white;
  margin-bottom: 100px;
}

.App-footer {
  height: 100px;
}

.App-link {
  color: #61dafb;
}

.product-img {
  width: 400px;
}

.shopping-cart {
  position: fixed;
  right: 16px;
  top: 16px;
  cursor: pointer;
}

.contact-links {
  display: flex;
  flex-direction: column;
  text-align: right;
  position: fixed;
  right: 16px;
  bottom: 16px;
  height: 40px;
  justify-content: space-between;
}

.product-name {
  font-size: 30px;
  margin-bottom: 10px;
}

.product-price {
  font-size: 20px;
  margin-bottom: 10px;
}

.product-variants {
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 10px;
}

.size {
  cursor: pointer;
  width: 80px;
}

.size:hover {
  font-weight: 800;
  color: orange;
}

.selected {
  width: 80px;
  font-weight: 800;
  box-sizing: border-box;
  border-style: solid;
  border-width: 3px;
}

.add-to-cart {
  font-size: 20px;
  cursor: pointer;
}

.add-to-cart:hover {
  color: orange;
}

.sold-out {
  font-size: 20px;
}

a {
  color: white;
}

.cart-item {
  width: 400px;
  display: grid;
  grid-template-columns: 1fr 1fr 4fr 1fr 1fr;
  grid-gap: 10px;

  margin: 10px 0px 10px 0px;
}

.remove-item {
  text-align: center;
  font-size: 18px;
  font-weight: 900;
  cursor: pointer;
  grid-column: 1 / 2;
  width: 20px;
}

.remove-item:hover {
  color: red;
}

.item-quantity {
  grid-column: 2 / 3;
}

.item-name {
  grid-column: 3 / 4;
}

.item-size {
  grid-column: 4 / 5;
}

.item-price {
  grid-column: 5 / 6;
}

.ReactModal__Body--open {
  overflow-y: hidden;
}

.ReactModal__Overlay {
  opacity: 0;
  transition: opacity 200ms ease-in-out;
}

.ReactModal__Overlay--after-open {
  opacity: 1;
}

.ReactModal__Overlay--before-close {
  opacity: 0;
}

.Modal {
  position: absolute;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
}

.Overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
